@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  color: black;
}

a {
  text-decoration: none;
  font-style: normal;
  color: black;
}

::-webkit-scrollbar {
  display: none;
}

.fadeIn {
  animation: fadeIn 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10%);
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

.fadeInLeft {
  animation: fadeInLeft 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(10%);
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.fadeInBasic {
  animation: fadeInBasic 2s;
  opacity: 1;
}

@keyframes fadeInBasic {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.app {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  line-height: 150%;
}

/* 
=================
BLOBS
=================
*/

.area {
  width: 100%;
  height: 100%;
  z-index: -1000;
  position: absolute;
  margin: 0;
  bottom: 0;
  padding: 0;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.circles li {
  margin: 0;
  padding: 0;
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(77, 132, 184, 0.446);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 10%;
  }

  100% {
    transform: translateY(-1800px) rotate(720deg);
    opacity: 0;
    border-radius: 90%;
  }
}

.homePage {
  display: flex;
  width: 60%;
  margin: 5em 0;
  font-family: "Raleway", sans-serif;
}

.homePage::-webkit-scrollbar {
  display: none;
}

.homePage .siteNavigation {
  display: flex;
  flex-direction: column;
  width: 35%;
}

.homePage .siteNavigation .headerDarkmode:after {
  left: 10px;
  content: "";
  width: 20px;
  height: 2px;
  opacity: 0;
  display: inline-block;
  animation: blink 0.9s linear infinite alternate;
  background: white;
}

.homePage .siteNavigation .headerLightmode:after {
  content: "";
  width: 15px;
  height: 2px;
  opacity: 0;
  display: inline-block;
  animation: blink 0.7s linear infinite alternate;
  background: black;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.homePage .siteNavigation .socialMediaLinksList {
  margin-top: 0;
  padding-left: 0;
  list-style-type: none;
}

.homePage .siteNavigation .socialMediaLinksList .socialMediaListItem {
  width: -webkit-max-content;
  width: max-content;
  margin-top: 0.5em;
  margin-right: 1em;
}

.homePage .siteNavigation .socialMediaLinksList .socialMediaListItem:nth-child(1) {
  margin-top: 0;
}

.homePage .siteNavigation .socialMediaLinksList a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}

.homePage .siteNavigation .socialMediaLinksList a .socialMediaIcon {
  margin-right: 0.1em;
  width: 16px;
  height: 16px;
  font-weight: bold;
  background-size: cover;
}

.homePage .siteNavigation .pageLinks {
  padding-left: 0;
  list-style-type: none;
  font-size: 20px;
  color: black;
}

.homePage .siteNavigation .pageLinks li {
  width: 90%;
}

.homePage .siteNavigation .pageLinks li:nth-child(2) {
  margin-top: 0.5em;
}

.homePage .siteNavigation .menuAndSwitchContainer {
  display: flex;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer {
  padding-top: 5px;
  padding-left: 4px;
  padding-bottom: 30px;
  width: -webkit-max-content;
  width: max-content;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switchTitle {
  padding-bottom: 15px;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch input {
  display: none;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch input:checked + .slider:before {
  transform: translateX(26px);
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(77, 132, 184, 0.446);
  transition: 0.4s;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #e0e0e0;
  transition: 0.4s;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch .slider.round {
  border-radius: 34px;
}

.homePage .siteNavigation .menuAndSwitchContainer .themeSwitchingContainer .switch .slider.round:before {
  border-radius: 50%;
}

.homePage .displayedPage {
  width: 65%;
  overflow-x: hidden;
}

@media only screen and (max-width: 1100px) {
  .homePage {
    width: 90%;
    flex-direction: column;
    margin: 2em 0;
  }
  .homePage .siteNavigation {
    border-right: none;
    border-bottom: 1px solid black;
    width: 100%;
  }
}

@media only screen and (max-width: 1100px) and (max-width: 500px) {
  .homePage .siteNavigation h1 {
    margin: 0;
    margin-bottom: 10px;
    width: -webkit-max-content;
    width: max-content;
  }
}

@media only screen and (max-width: 1100px) {
  .homePage .siteNavigation .socialMediaLinksList .socialMediaListItem {
    display: inline-block;
  }
}

@media only screen and (max-width: 1100px) and (max-width: 500px) {
  .homePage .siteNavigation .menuAndSwitchContainer {
    justify-content: space-between;
  }
}

@media only screen and (max-width: 1100px) and (max-width: 600px) {
  .homePage .siteNavigation .pageLinks {
    margin-top: 0;
  }
}

@media only screen and (max-width: 1100px) and (max-width: 500px) {
  .homePage .siteNavigation .pageLinks {
    display: none;
  }
}

@media only screen and (max-width: 1100px) {
  .homePage .siteNavigation .pageLinks li {
    width: 100%;
  }
  .homePage .displayedPage {
    width: 100%;
  }
  ::-webkit-scrollbar {
    display: none;
  }
}

@media only screen and (min-width: 1700px) {
  .homePage {
    width: 45%;
  }
}
