/* BLACK UNDERLINE ANIMATION */

.aboutMeList a,
.projectLink,
.socialMediaLinksList a {
  font-style: italic;
  position: relative;
}

.aboutMeList a:hover,
.projectLink:hover,
.socialMediaLinksList a:hover {
  font-weight: 900;
}

.aboutMePage a:before,
.projectLink:before,
.socialMediaLinksList a:before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: rgba(77, 132, 184, 0.446);
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}
.aboutMePage a:hover:before,
.projectLink:hover:before,
.socialMediaLinksList a:hover:before {
  visibility: visible;
  transform: scaleX(1);
}

/* PAGE LINK (SCALE/GREY COVER) ANIMATION */

.pageLinks li,
.projectsList li {
  padding: 0.2em;
  border-radius: 0.3em;
  transition: 0.5s ease-in-out;
  background: linear-gradient(
    to left,
    rgba(255, 255, 255, 0) 50%,
    rgba(211, 211, 211, 0.52) 50%
  );
  background-size: 200% 100%;
  background-position: right bottom;
}

.pageLinks li:hover,
.projectsList li:hover {
  background-position: left bottom;
  transform: scale(1.02);
  cursor: pointer;
  font-weight: 900;
  z-index: 9999;
  color: black;
}

/*
UNDERLINE DECORATION
*/

.pageLinksUnderline {
  text-decoration: underline;
  text-underline-offset: 4px;
}

.menuContainer .switch {
  display: none;
  cursor: pointer;
  width: -webkit-max-content;
  width: max-content;
}

@media only screen and (max-width: 500px) {
  .menuContainer .switch {
    display: inline-block;
    width: 80px;
  }
}

.menuContainer .switch .bar1,
.menuContainer .switch .bar2,
.menuContainer .switch .bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

.menuContainer .switch.change .bar1 {
  transform: rotate(-45deg) translate(-9px, 6px);
}

.menuContainer .switch.change .bar2 {
  opacity: 0;
}

.menuContainer .switch.change .bar3 {
  transform: rotate(45deg) translate(-8px, -8px);
}

.menuContainer .switch.change .dropDownMenu {
  display: inline;
}

.menuContainer .dropDownMenu {
  display: none;
}

@media only screen and (max-width: 500px) {
  .menuContainer .dropDownMenu {
    display: flex;
    flex-direction: column;
  }
}

.menuContainer .dropDownMenu.noChange {
  animation: menuPopupReverse 1s;
}

.menuContainer .dropDownMenu.noChange .dropDownPageLink {
  display: none;
}

.menuContainer .dropDownMenu.change {
  animation: menuPopup 0.5s;
  animation-fill-mode: forwards;
}

.menuContainer .dropDownMenu.change .dropDownPageLink {
  animation: menuItemPopup 3s;
}

.menuContainer .dropDownMenu.change .dropDownPageLink:hover {
  cursor: pointer;
}

@keyframes menuPopup {
  0% {
    height: 0;
  }
  100% {
    height: 50px;
  }
}

@keyframes menuPopupReverse {
  0% {
    height: 50px;
  }
  100% {
    height: 0px;
  }
}

@keyframes menuItemPopup {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*# sourceMappingURL=MenuButton.css.map */
